import "./css/AdoptionEdit.css";
// import {useParams} from "react-router-dom";
import MyHeader from "../../components/MyHeader";
import MyFooter from "../../components/MyFooter";
import CrudButton from "../../components/CrudButton";
import EditInput from '../../components/EditInput';
import MyButton from '../../components/MyButton';
import AdoptionTumbnail from './AdoptionThumbnail';
import { useState } from 'react';

const AdoptionEdit = ({onCreate}) => {

    // const {id} = useParams();
    // console.log(` id : ${id}`);

    const [state, setState] = useState({
        job: '',
        income: '',
    });

    const adoptionState = (state_name, value) => {
        setState({
            ...state,
            [state_name]: value,
        })
    }

    // const handleSubmit = () => {
    //     // onCreate(name, age, sex, neutering, identification)
    // }

    return (
        <div>
        <MyHeader />
        <div className='AdoptionEdit'>
            <div className='AdoptionEdit_left'>
            <div className={['AdoptionEdit_form', 'AdoptionEdit_form_user'].join(" ")}>
            <h2>  </h2>
            <h3>  </h3>
                <EditInput
                    type={'job'}
                    name=""
                    placeholder={"   ?"}
                    setState={adoptionState}
                />
                <EditInput
                    type={'income'}
                    name=""
                    placeholder={"    ?"}
                    setState={adoptionState}
                />
                <MyButton text={"+   "} />    
            </div>

            <div className={['AdoptionEdit_form', 'AdoptionEdit_form_pet'].join(" ")}>
                <h3>  </h3>
                <h5>   ?</h5>
                <MyButton type={"true_false"} text={""} />
                <MyButton type={"true_false"} text={""} />
                <EditInput
                    type={'species'}
                    name=""
                    placeholder={"   ?"}
                    setState={adoptionState}
                />
                <EditInput
                    type={'period'}
                    name=""
                    placeholder={"    ?"}
                    setState={adoptionState}
                />
                <MyButton text={"+   "} />
            </div>

            <div className={['AdoptionEdit_form', 'AdoptionEdit_form_atmosphere'].join(" ")}>
                <h3>  </h3>
                <EditInput 
                    type={'use_hour'}
                    name=""
                    placeholder={"      ?"}
                    setState={adoptionState}
                />
                <EditInput 
                    type={'use_money'}
                    name=""
                    placeholder={"     ?"}
                    setState={adoptionState}
                />
                <EditInput 
                    type={'use_family'}
                    name=""
                    placeholder={"   ?"}
                    setState={adoptionState}
                />
                <EditInput 
                    type={'use_permission'}
                    name=""
                    placeholder={"    ?"}
                    setState={adoptionState}
                />
            </div>
                <input type={'checkbox'} />
                <label>    </label>
                <div className='AdoptionEdit_form_permission'>
                    <h6>1.        .</h6>
                    <h6>2.          1  .</h6>
                </div>
                <CrudButton 
                    text={' '}
                    type={'submit'}
                    onClick={() => alert(".")}
                />
            </div>
            
                <div className='AdoptionEdit_right'>
                    <h5>  </h5>
                    <AdoptionTumbnail 
                        name={""}
                        age={"8"} 
                        sex={""}
                        neutering={true}
                        identification={true}
                    />
                </div>
                {/* <CrudButton
                    text={''}
                    type={'submit'}
                    onClick={() => alert(".")}
                />
                <CrudButton
                    text={''}
                    type={'edit'}
                    onClick={() => alert(".")}
                />
                <CrudButton
                    text={''}
                    type={'delete'}
                    onClick={() => alert(".")}
                /> */}
        </div>
            <MyFooter />
        </div>
    );
}

export default AdoptionEdit;