import { useState } from "react";

import "./css/CommList.css";
import CommCate_item from "./component/CommCate_item";
import Comm_item from "./component/Comm_item";
import MyPagination from "../../components/MyPagination";

const listcategory = [
  { category_id: 1, category_descript: "" },
  { category_id: 2, category_descript: "" },
  { category_id: 3, category_descript: "" },
  { category_id: 4, category_descript: "" },
  { category_id: 5, category_descript: "" },
];

const CommList = () => {
  const [category, setCategory] = useState(1);

  const handleClickCategory = (category) => {
    setCategory(category);
  };

  return (
    <div className="CommList">
      <div className="category_wrapper">
        {listcategory.map((it) => (
          <CommCate_item
            key={it.category_id}
            {...it}
            onClick={handleClickCategory}
            isSelected={it.category_id === category}
          />
        ))}
      </div>
      <Comm_item
        id={1}
        name={""}
        grade={"top"}
        time={1}
        text={
          "   .        .          .        ?"
        }
        heart={15}
        reply={11}
      />
      <Comm_item
        id={2}
        name={""}
        grade={"mid"}
        time={2}
        text={
          "   .        .          .        ?"
        }
        heart={16}
        reply={10}
      />
      <Comm_item
        id={3}
        name={""}
        grade={"top"}
        time={3}
        text={
          "   .        .          .        ?"
        }
        heart={17}
        reply={15}
      />
      <Comm_item
        id={4}
        name={""}
        grade={"bot"}
        time={4}
        text={
          "   .        .          .        ?"
        }
        heart={18}
        reply={14}
      />
      <Comm_item
        id={5}
        name={"Journey"}
        grade={"top"}
        time={5}
        text={
          "   .        .          .        ?"
        }
        heart={19}
        reply={5}
      />
      <MyPagination />
    </div>
  );
};

export default CommList;
