import "./css/MyFooter.css";
import MyButton from "./MyButton";
import { useNavigate } from "react-router-dom";

const MyFooter = () => {
  const navigate = useNavigate();

  const goMain = () => {
    navigate(`/`);
  };

  return (
    <div className="MyFooter">
      <div className="MyFooter_leftText">
        <h5> |  | </h5>
        <div>
          <b></b>     1F
        </div>
        <div>
          <b>Tel.</b> 010 - 1234 - 5678
        </div>
        <div>
          <b>.</b>  111 - 0000 - 0000 -11  
        </div>
      </div>
      <div className="MyFooter_rightText">
        <MyButton text={""} type={"head_logo"} onClick={goMain} />
        <div>@ DalisHouse</div>
        <div> DalisHouse.2022.All rights reserved</div>
      </div>
      <div className="MyFooter_finishLine"> </div>
    </div>
  );
};

export default MyFooter;
