import "./css/VolunteerActivityEdit.css"
import { useState } from 'react';
import CrudButton from '../../components/CrudButton';
import EditInput from '../../components/EditInput';
import MyButton from '../../components/MyButton';
import MyFooter from '../../components/MyFooter';
import MyHeader from '../../components/MyHeader';

const VolunteerActivityEdit = () => {

    const [state, setState] = useState({
        name: '',
        birthYear: '',
        birthMonth: '',
        birthDay: '',
        sex: null,
        tel: '',
        email: '',
    });

    const setVolunteerActivity = (state_name, value) => {
        setState({
            ...state,
            [state_name]: value,
        });
    };

    return (
        <div>
            <MyHeader />
            <div className='VolunteerActivityEdit'>
                <div className='VolunteerActivityEdit_left'>
                    <h2>  </h2>
                    <EditInput 
                        type={'name'}
                        name={''}
                        placeholder={' '}
                        setState={setVolunteerActivity}
                    />
                    <EditInput 
                        type={'birth'}
                        name={''}
                        placeholder={' '}
                        setState={setVolunteerActivity}
                    />
                    <h5></h5>
                    <MyButton type={"true_false"} text={""} />
                    <MyButton type={"true_false"} text={""} />
                    <EditInput 
                        type={'tel'}
                        name={''}
                        placeholder={' '}
                        setState={setVolunteerActivity}
                    />
                    <EditInput 
                        type={'email'}
                        name={''}
                        placeholder={' '}
                        setState={setVolunteerActivity}
                    />

                    <div className='VolunteerActivityEdit_left_submit'>
                        <input type={'checkbox'} />
                        <label>    </label>
                        <div className='VolunteerActivityEdit_left_submit_permission'>
                            <h6>1.   <br />  ,     </h6>
                            <h6>2.  <br /> [], ,  ,  </h6>
                            <h6>3.   <br /> </h6>
                            <h6>4.        . ,           .</h6>
                        </div>
                        <CrudButton
                            text={' '}
                            type={'submit'}
                            onClick={() => alert(".")}
                        />
                    </div>
                </div>
                <div className='VolunteerActivityEdit_right'>

                </div>
            </div>
            <MyFooter />
        </div>
    );
}

export default VolunteerActivityEdit;